import React, { useState, useEffect } from 'react';
import BarChart from './BarChart';

const LoansInfoCard = ({ loanNumber }) => {
  const [selectedLoan, setSelectedLoan] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('http://localhost:3001/personloaninfo'); 
        const data = await response.json();
        const selected = data[loanNumber];
        setSelectedLoan(selected);
      } catch (error) {
        console.error('     :', error);
      }
    };

    fetchData();
  }, [loanNumber]);

  const calculateRemainingAmount = () => {
    if (selectedLoan) {
      return selectedLoan.LoanAmount - selectedLoan.PaidOffAmount + selectedLoan.LoanAmount * (selectedLoan.LoanTerm / 12)* selectedLoan.AnnualInterestRate/100;
    }
    return 0;
  };
  const renderBarChart = () => {
    if (selectedLoan) {
      const remainingAmount = calculateRemainingAmount();
      const data = {
        labels: [' ', ' ', ' '],
        values: [selectedLoan.LoanAmount, selectedLoan.MonthlyPayment, remainingAmount],
      };

      return <BarChart data={data} />;
    }
    return null;
  };
  if (!selectedLoan) {
    return(
    <div className='loan-info'>
      <p>   </p>;
    </div>
    ) 
  }


  return (
    <div>
      {selectedLoan && (
        <div className='loan-info'>
          <div className='Canva'>
          {renderBarChart()}
          </div>
          <div className='dataInfo'>  
          <h2> </h2>
          <p>{selectedLoan.LoanName}</p>
          <p> : {selectedLoan.AnnualInterestRate}% </p>
          <p> : {selectedLoan.LoanTerm} </p>
          <p> : ${selectedLoan.LoanAmount}</p>
          <p> : ${selectedLoan.MonthlyPayment}</p>
          <p>   : {selectedLoan.EarlyRepaymentPenalties}</p>
          <p>  : ${selectedLoan.PaidOffAmount}</p>
          <p> : ${calculateRemainingAmount()}</p>
          </div>
        </div>
      )}
    </div>
  );
};

export default LoansInfoCard;
