import React, { useState } from 'react';
import axios from 'axios';

const Poluch = (props) => {
  const [data, setData] = useState('');

  const fetchData = async (format) => {
    try {
      const response = await axios.get(`http://localhost:3002/getData/${props.code}/${format}`);
      setData(response.data);
    } catch (error) {
      console.error('   :', error);
    }
  };

  return (
    <div className='ReplenishmentCont'>
      <button onClick={() => fetchData('json')}> JSON</button>
      <button onClick={() => fetchData('xml')}> XML</button>
      <button onClick={() => fetchData('html')}> HTML</button>
      <div>
        <pre>{typeof data === 'object' ? JSON.stringify(data, null, 2) : data}</pre>
      </div>
    </div>
  );
};

export default Poluch;

